/* STATIC STYLES (desktop-first) */

.about {
  display: flex;
  flex-direction: column;

  gap: 5rem;
}

.about-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  gap: 2rem;
}

.about-cards > * {
  flex: 0 0 20rem;
}

.about-card-content {
  display: flex;
  flex-direction: column;
  align-items: center;

  text-align: center;

  gap: 1rem;
}

.technologies {
  display: flex;
  justify-content: center;

  margin-top: 1rem;

  gap: 1rem;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
}

@media (max-width: 768px) {
  .about {
    padding-inline: 8rem;
  }
}

@media (max-width: 576px) {
  .about {
    padding-inline: 2rem;
  }

  .about-cards {
    flex-direction: column;
  }

  .about-cards > * {
    flex: 1;
  }
}
