/* STATIC STYLES (desktop-first) */

.tilt-card1 {
  padding: 1px;

  border-radius: 2rem;

  background: linear-gradient(135deg, var(--color-primary-text-dark), var(--color-secondary));
  box-shadow: 0 35px 120px -15px var(--color-card);

  transform-style: preserve-3d;

  will-change: transform;
}

.tilt-card1-inner {
  height: 100%;
  padding: 3rem;

  border-radius: 2rem;

  background-color: var(--color-primary);

  will-change: transform;
}

